import ListChoose from '@/components/ListChoose/ListChoose.vue'
import ProcessModel from '@/models/process.js'
import { formatDate } from '@/utils/utils.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const processModel = new ProcessModel()
export default {
	data() {
		return {
			resumeData: null,
			processData: [],
			loginType: '',
			date: '',
			entryTime: '',
			timeText: '面试',
			inputContent: '', // 申诉内容
			tempItem: ''
		};
	},
	components:{
		ListChoose,
		uniPopup
	},
	onLoad(option){
		this.resumeData = option
		this.loginType = uni.getStorageSync('loginType')
		this.getProcessList()
	},
	filters: {
		filterDate(value){
			return formatDate(value,'yyyy/MM/dd hh:mm:ss')
		}
	},
	methods: {
		showResumeDetail(){
			const that = this
			uni.navigateTo({
				url: '../addResume/addResume?id='+this.resumeData.resumeId+'&isEdit=false',
				success() {
					uni.setNavigationBarTitle({
						title: that.resumeData.name+'的简历'
					})
				}
			})
		},
		getProcessList(){
			const that = this
			uni.showLoading({
				mask: true
			})
			processModel.processList(this.resumeData.receviedId).then(res=>{
				uni.hideLoading()
				const { code, message, data } = res.data
				if(code === '0'){
					// 因为后台的愚蠢而不得不做的处理
					const tempData = data
					tempData.list.map(item=>{
						if(item.button_a){
							item.button_a = JSON.parse(item.button_a)
						}
						if(item.button_b){
							item.button_b = JSON.parse(item.button_b)
						}
					})
					that.processData = tempData
					
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '获取流程信息失败'
				})
			})
		},
		handleBtnClick(btn, item){
			const id = item.id
			// 面试反馈
			if(btn.text === '面试通过'){
				this.tempInfo = {
					method: 'interviewFeedback',
					options:{
						id,
						status:'0'
					}
				}
				this.timeText = '入职'
				this.$refs.selectDate.open()
				// this.interviewFeedback(id,0,entryTime)
			}
			if(btn.text === '未到场'){
				this.interviewFeedback(id,'1')
			}
			if(btn.text === '未通过'){
				this.interviewFeedback(id,'2')
			}
			if(btn.text === '修改面试时间'){
				this.tempInfo = {
					method: 'interviewFeedback',
					options:{
						id,
						status:'3'
					}
				}
				this.timeText = '面试'
				this.$refs.selectDate.open()
			}
			// A取消简历
			if(btn.text === '取消面试'){
				this.cancelResume(id)
			}
			if(btn.text === '已知晓'){
				this.hasKnown(item)
			}
			if(btn.text === '申诉'){
				this.appeal(item)
			}
			if(btn.text === '认可'){
				this.approval(item)
			}
			// 入职反馈
			if(btn.text === '修改入职时间'){
				this.tempInfo = {
					method: 'entryFeedback',
					options:{
						id,
						status:'2'
					}
				}
				this.timeText = '入职'
				this.$refs.selectDate.open()
			}
			if(btn.text === '已入职'){
				this.tempInfo = {
					method: 'entryFeedback',
					options:{
						id,
						status:'0'
					}
				}
				this.timeText = '入职'
				this.$refs.selectDate.open()
			}
			if(btn.text === '未入职'){
				this.entryFeedback(id,1)
			}
			if(btn.text === '已离职'){
				this.tempInfo = {
					method: 'quitFeedBack',
					options:{
						id
					}
				}
				this.timeText = '离职'
				this.$refs.selectDate.open()
			}
		},
		push(){
			if(this.tempInfo.method === 'interviewFeedback'){
				const array = Object.values(this.tempInfo.options)
				array.push(this.date)
				this.interviewFeedback(...array)
			}else if(this.tempInfo.method === 'entryFeedback'){
				const array = Object.values(this.tempInfo.options)
				array.push(this.date)
				this.entryFeedback(...array)
			}else if(this.tempInfo.method === 'quitFeedBack'){
				const array = Object.values(this.tempInfo.options)
				array.push(this.date)
				this.quitFeedBack(...array)
			}
		},
		// 提交面试反馈
		interviewFeedback(id,status,time){
			uni.showLoading({
				mask: true
			})
			const that = this
			processModel.interviewFeedback(id,status,time).then(res=>{
				uni.hideLoading()
				that.$refs.selectDate.close()
				const { code, message, data } = res.data
				if(code === '0'){
					// 这里做后续处理
					that.getProcessList()
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '提交失败'
				})
			})
		},
		// 提交入职反馈
		entryFeedback(id,status,time){
			uni.showLoading({
				mask: true
			})
			const that = this
			processModel.entryFeedBack(id,status,time).then(res=>{
				uni.hideLoading()
				that.$refs.selectDate.close()
				const { code, message, data } = res.data
				if(code === '0'){
					// 这里做后续处理
					that.getProcessList()
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '提交失败'
				})
			})
		},
		quitFeedBack(id,time){
			uni.showLoading({
				mask: true
			})
			const that = this
			processModel.quitFeedBack(id,time).then(res=>{
				uni.hideLoading()
				that.$refs.selectDate.close()
				const { code, message, data } = res.data
				if(code === '0'){
					// 这里做后续处理
					that.getProcessList()
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '提交失败'
				})
			})
		},
		// 取消选择
		cancelPush(){
			this.tempInfo = {}
			this.$refs.selectDate.close()
		},
		bindDateChange(e){
			this.date = e.target.value
		},
		bindTimeStartChange(e){
			this.timeStart = e.target.value
		},
		bindTimeEndChange(e){
			this.timeEnd = e.target.value
		},
		// A端取消简历
		cancelResume(id){
			uni.showLoading({
				title: '取消中'
			})
			processModel.cancelResume(id).then(res=>{
				uni.hideLoading()
				const { code, message, data } = res.data
				if(code === '0'){
					uni.showToast({
						title: '取消简历成功'
					})
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})	
					},2000)
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '取消失败'
				})
			})
		},
		hasKnown(item){
			uni.showLoading({
				title: '请稍等...'
			})
			const that = this
			processModel.isKnow(item.id).then(res=>{
				uni.hideLoading()
				const { code, message, data } = res.data
				if(code === '0'){
					that.getProcessList()
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '取消失败'
				})
			})
		},
		appeal(item){
			this.tempItem = item
			this.$refs.inputDialog.open()
		},
		appealSubmit(){
			uni.showLoading({
				title: '请稍等...'
			})
			const item = this.tempItem
			const that = this
			processModel.appeal(item.id,this.inputContent).then(res=>{
				uni.hideLoading()
				that.$refs.inputDialog.close()
				const { code, message, data } = res.data
				if(code === '0'){
					that.getProcessList()
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '提交失败'
				})
			})
		},
		appealCancel(){
			this.$refs.inputDialog.close()
		},
		// B 认可
		approval(item){
			uni.showLoading({
				title: '请稍等...'
			})
			const that = this
			processModel.approval(item.id,this.inputContent).then(res=>{
				uni.hideLoading()
				const { code, message, data } = res.data
				if(code === '0'){
					this.processData.total += 1
					this.processData.list.push({
						create_time: new Date(),
						process_content: '认可',
						sort_number: item.sort_number + 1,
						owner: item.owner == 1? 0:1
					})
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '提交失败'
				})
			})
		}
	}
}
